.card:hover {
    position: relative;
    box-shadow: 3 3px 6px #999, 10 3px 10px #999;
    background-position: 50%; 
    width: 222px;
    margin: 50px 50px 3px 90px;
    border: 7px solid#ffc200; 
    float: center;
    background-color: #d80000;
    -webkit-transform: scale(1.05);
    -ms-transform: scale(1.05);
    transform: scale(1.05);
    -webkit-box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
    box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
    -webkit-transition: .1s;
    -o-transition: .1s;
    transition: .1s;
  
}

.card {
    position: relative;
    margin: 1rem 0 1rem 0;
    background-color: black;
    transition: box-shadow .001s;
    border-radius: 2px; 
}

.card-panel {
  transition: box-shadow .5s;
  padding: 10px;
  margin: 1rem 0 1rem 0;
  border-radius: 2px;
  background-color:#65747f;
  
}

.container-fluid {
  height: 175px;
  background-image: url("https://pngimage.net/wp-content/uploads/2018/05/bokeh-effect-png-2.png")
}

.shake {
  animation: shake 0.15s; 
  animation-iteration-count: 10; 
}
@keyframes shake {
  0% { transform: translate(0px, 0px); }
  50% { transform: translate(4px, 0px); }
  100% { transform: translate(-4px, 0px); }
}

